export default ()=> {
      return (
        <div className="question" data-index="134" dangerouslySetInnerHTML = {{ __html: `
<h6>134. 我们怎样才能在 <code>index.js</code> 中调用 <code>sum.js?</code> 中的 <code>sum</code>？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token comment">// sum.js</span>
<span class="token keyword keyword-export">export</span> <span class="token keyword keyword-default">default</span> <span class="token keyword keyword-function">function</span> <span class="token function">sum</span><span class="token punctuation">(</span><span class="token parameter">x</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
	<span class="token keyword keyword-return">return</span> x <span class="token operator">+</span> x<span class="token punctuation">;</span>
<span class="token punctuation">}</span>

<span class="token comment">// index.js</span>
<span class="token keyword keyword-import">import</span> <span class="token operator">*</span> <span class="token keyword keyword-as">as</span> sum <span class="token keyword keyword-from">from</span> <span class="token string">"./sum"</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>sum(4)</code></li>
<li>B: <code>sum.sum(4)</code></li>
<li>C: <code>sum.default(4)</code></li>
<li>D: 默认导出不用 <code>*</code> 来导入，只能具名导出</li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: C</h4>
<p>使用符号 <code>*</code>，我们引入文件中的所有值，包括默认和具名。如果我们有以下文件：</p>
<pre class="language-javascript"><code class="language-javascript"><span class="token comment">// info.js</span>
<span class="token keyword keyword-export">export</span> <span class="token keyword keyword-const">const</span> name <span class="token operator">=</span> <span class="token string">"Lydia"</span><span class="token punctuation">;</span>
<span class="token keyword keyword-export">export</span> <span class="token keyword keyword-const">const</span> age <span class="token operator">=</span> <span class="token number">21</span><span class="token punctuation">;</span>
<span class="token keyword keyword-export">export</span> <span class="token keyword keyword-default">default</span> <span class="token string">"I love JavaScript"</span><span class="token punctuation">;</span>

<span class="token comment">// index.js</span>
<span class="token keyword keyword-import">import</span> <span class="token operator">*</span> <span class="token keyword keyword-as">as</span> info <span class="token keyword keyword-from">from</span> <span class="token string">"./info"</span><span class="token punctuation">;</span>
console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>info<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<p>将会输出以下内容：</p>
<pre class="language-javascript"><code class="language-javascript"><span class="token punctuation">{</span>
  <span class="token keyword keyword-default">default</span><span class="token operator">:</span> <span class="token string">"I love JavaScript"</span><span class="token punctuation">,</span>
  name<span class="token operator">:</span> <span class="token string">"Lydia"</span><span class="token punctuation">,</span>
  age<span class="token operator">:</span> <span class="token number">21</span>
<span class="token punctuation">}</span>
</code></pre>
<p>以 <code>sum</code> 为例，相当于以下形式引入值 <code>sum</code>：</p>
<pre class="language-javascript"><code class="language-javascript"><span class="token punctuation">{</span> <span class="token function-variable function">default</span><span class="token operator">:</span> <span class="token keyword keyword-function">function</span> <span class="token function">sum</span><span class="token punctuation">(</span><span class="token parameter">x</span><span class="token punctuation">)</span> <span class="token punctuation">{</span> <span class="token keyword keyword-return">return</span> x <span class="token operator">+</span> x <span class="token punctuation">}</span> <span class="token punctuation">}</span>
</code></pre>
<p>我们可以通过调用 <code>sum.default</code> 来调用该函数</p>
</p>
</details>
` }}></div>
      );
    }